import Vue from 'vue'
import Router from 'vue-router'
export const routes = [
    {
        path: '/',
        name: 'login',

        meta: {
            title: '登陆'
        },
        component: (resolve) => require(['@/page/login/login'], resolve)
    }, 
   
    
    {
        path: '/home',
        name: 'home',

        meta: {
            title: '主页'
        },
        component: (resolve) => require(['@/page/home'], resolve),
        children: [
                      
            {
                path: '/reportManage',
                name: 'reportManage',
                meta: {
                    title: '报表管理'
                },
                component: (resolve) => require(['@/page/reportManage'], resolve),
            },             
         
        ]
    },
]
Vue.use(Router);
export const router = new Router({
    // mode: 'history',
    routes,
});