import axios from 'axios'
import qs from 'qs'
import Cookies from 'js-cookie';
const instance = axios.create({
  // baseURL: 'http://game.cstor.cn/game/',
  baseURL: 'http://game.cstor.cn:9999/game/',
  // baseURL: 'http://192.168.0.239:9999/game/',
  timeout: 600000,   //请求超时将会中断
  
});
