export default {
  post(url, data) {
    return instance({
      method: 'post',
      url,
      data: qs.stringify(data),
      headers: {
        'token': Cookies.get('_token')
      },
      timeout: 20000,
    }).then(
      (response) => {
        return response
      }
    ).catch(function(error) {
      //console.log("cuowou","网络异常");
    });
  },
  postJson(url, data) {
    return instance({
      headers: {
        'Content-Type': 'application/json',
        'token': Cookies.get('_token')
      },

      method: 'post',
      url,
      data: JSON.stringify(data),
      // data: data,
      timeout: 20000,
    }).then(
      (response) => {
        return response
      }
    ).catch(function (error) {
      //console.log("cuowou","网络异常");
    });
  },
  postformdata(url, fd) {
    return instance({
      method: 'post',
      url,
      data: fd,
      timeout: 20000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'Content-Type': 'multipart/form-data', //hearder 很重要，Content-Type 要写对
        'token': Cookies.get('_token')
      }
    }).then(
      (response) => {
        return response
      }
    ).catch(function(error) {
      
    })
  },
  get(url, params) {
    return instance({
      method: 'get',
      url,
      params, // get 请求时带的参数
      timeout: 20000,
      headers: {
        'X-Requested-With': 'XMLHttpRequest',
        'token': Cookies.get('_token')
      }
    }).then(
      (response) => {
        return response
      }
    ).catch(function(error) {
      //console.log("cuowou","网络异常");
    })
  },
}
